.hero {
    background: url(../assets/image-5.jpg);
    width: 100%;
    height: 85vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
    /* border: 2px solid blue; */
    display: flex;
    align-items: center;
}

.hero .container {
    /* border: 2px solid red; */
    height: auto;
    width: auto;
    color: #fff;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* margin: 2rem auto; */
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.hero .left {
    padding: 1rem;
}

.hero h1 {
    padding: 1rem 0;
}

.hero .input-container {
    border: #fff;
    padding: 1rem 0;
    color: #fff;
}

.hero .right {
    padding: 1rem;
}

.hero .img-container {
    width: 100%;
    display: flex;
}

.hero img {
    max-width: 80%;
    margin: auto;
}

@media screen and (max-width: 940px) {
    .hero .container {
        grid-template-columns: 1fr;
        margin: 1rem 0;
    }
}











